import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path:'/hello',
      name:'HelloWorld',
      component: HelloWorld
    }
  ]
})
